.vector {
  margin-left: 13px;
}
.franchise__btn {
  &:hover {
    background-color: #ecebeb;
  }
}
.contacts {
  background-image: linear-gradient(#ffffff 10%, #fff5f6 10%, #fff5f6 90%);
}
.contacts__title {
  display: none;
}

.contacts__btns {
  background-color: #fff5f6;
  text-align: center;
}

.contacts__packs {
  margin-bottom: 0;
}

.contacts__pack {
  background-color: #fff;
  border-radius: 24px;
  box-shadow: 0px 8px 30px rgba(212, 20, 67, 0.1);
}
.contacts__1category,
.contacts__2category,
.contacts__3category {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  line-height: 186%;
  display: flex;
  align-items: center;
  text-align: center;
  letter-spacing: 0.04em;
  color: #ffffff;
  justify-content: center;
}

.contacts__1category,
.contacts__3category {
  background-color: #f0d1a5;
}

.contacts__2category {
  background-color: #c2e297;
}
.contacts__city {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  line-height: 186%;
  letter-spacing: 0.04em;
  color: #000000;
}

.contacts__description {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  color: #907e82;
}
.contacts__open {
  border-top: 1px solid #e1e1e1;
  border-bottom: 1px solid #e1e1e1;
}
.contacts__open-days {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  line-height: 186%;
  color: #000000;
}

.contacts__open-hours {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  line-height: 186%;
  color: #ffa5ba;
}

.contacts__contact-tel,
.contacts__contact-mail {
  font-family: DM Sans;
  font-style: normal;
  font-weight: 500;
  line-height: 150%;
  color: #907e82;
}

@media screen and (min-width: 320px) and (max-width: 767px) {
  .contacts__pack {
    width: 220px;
    height: 427px;
    padding: 43px 21px;
    padding-left: 21px;
    &:nth-child(1) {
      margin-bottom: 20px;
    }
    &:nth-child(2) {
      margin-bottom: 20px;
    }
  }

  .contacts__in {
    padding-left: 21px;
  }
  .contacts__1category,
  .contacts__2category,
  .contacts__3category {
    font-size: 12px;
  }

  .contacts__cities {
    padding-top: 25px;
    padding-bottom: 25px;
    padding-left: 21px;
  }
  .contacts__city {
    font-size: 12px;
    margin-top: 0px;
    margin-bottom: 10px;
  }
  .contacts__description {
    font-size: 12px;
    margin: 0;
  }

  .contacts__open {
    padding-top: 25px;
    padding-bottom: 25px;
    margin-left: 21px;
  }

  .contacts__open-days {
    font-size: 12px;
    margin: 0;
  }

  .contacts__open-hours {
    font-size: 12px;
    margin: 0;
    padding-bottom: 10px;
  }

  .contacts__contact {
    padding-top: 25px;
    margin-left: 21px;
  }
  .contacts__contact-tel,
  .contacts__contact-mail {
    font-size: 12px;
  }

  .contacts__1category,
  .contacts__3category {
    width: 80px;
    height: 16px;
    border-radius: 4px;
    margin-left: 21px;
  }

  .contacts__2category {
    width: 130px;
    height: 16px;
    border-radius: 4px;
    margin-left: 21px;
  }

  .contacts__btns {
    padding-bottom: 113px;
    padding-top: 54px;
  }

  .franchise__btn {
    width: 161px;
    height: 44px;
    border-radius: 22px;
    background: #ffffff;
    border: none;
    font-family: DM Sans;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.312;
    text-align: center;
    color: #d41443;
    margin-top: 10px;
  }

  .locations__btn {
    width: 161px;
    height: 44px;
    border-radius: 22px;
    background: #d41443;
    border: none;
    font-family: DM Sans;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: 1.312;
    text-align: center;
    color: #fff;
  }
}

